body {
  text-transform: uppercase;
  --blue: #00acdc;
  --white: #fff;
  background-color: gray;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: var(--white);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header__image {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  margin-right: 50px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--blue);
}

.is-active {
  color: var(--blue);
  font-size: 12px;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

